class TrieNode {
    constructor() {
        this.terminal = false;
        this.children = new Map();
    }
}
export class Trie {
    constructor() {
        this.root = new TrieNode();
        this.elements = 0;
    }
    get length() {
        return this.elements;
    }
    get(key) {
        const node = this.getNode(key);
        if (node) {
            return node.value;
        }
        return null;
    }
    contains(key) {
        const node = this.getNode(key);
        return !!node;
    }
    insert(key, value) {
        let node = this.root;
        let remaining = key;
        while (remaining.length > 0) {
            let child = null;
            for (const childKey of node.children.keys()) {
                const prefix = this.commonPrefix(remaining, childKey);
                if (!prefix.length) {
                    continue;
                }
                if (prefix.length === childKey.length) {
                    // enter child node
                    child = node.children.get(childKey);
                    remaining = remaining.slice(childKey.length);
                    break;
                }
                else {
                    // split the child
                    child = new TrieNode();
                    child.children.set(childKey.slice(prefix.length), node.children.get(childKey));
                    node.children.delete(childKey);
                    node.children.set(prefix, child);
                    remaining = remaining.slice(prefix.length);
                    break;
                }
            }
            if (!child && remaining.length) {
                child = new TrieNode();
                node.children.set(remaining[0], child);
                remaining = remaining.slice(1);
            }
            node = child;
        }
        if (!node.terminal) {
            node.terminal = true;
            this.elements += 1;
        }
        node.value = value;
    }
    remove(key) {
        const node = this.getNode(key);
        if (node) {
            node.terminal = false;
            this.elements -= 1;
        }
    }
    map(prefix, func) {
        const mapped = [];
        const node = this.getNode(prefix);
        const stack = [];
        if (node) {
            stack.push([prefix, node]);
        }
        while (stack.length) {
            const [key, node] = stack.pop();
            if (node.terminal) {
                mapped.push(func(key, node.value));
            }
            for (const c of node.children.keys()) {
                stack.push([key + c, node.children.get(c)]);
            }
        }
        return mapped;
    }
    getNode(key) {
        let node = this.root;
        let remaining = key;
        while (node && remaining.length > 0) {
            let child = null;
            for (let i = 1; i <= remaining.length; i += 1) {
                child = node.children.get(remaining.slice(0, i));
                if (child) {
                    remaining = remaining.slice(i);
                    break;
                }
            }
            node = child;
        }
        return remaining.length === 0 && node; // && node.terminal ? node : null;
    }
    commonPrefix(a, b) {
        const shortest = Math.min(a.length, b.length);
        let i = 0;
        for (; i < shortest; i += 1) {
            if (a[i] !== b[i]) {
                break;
            }
        }
        return a.slice(0, i);
    }
}
